



/* DEFAULT VALUES OF TAGES 
============================================================================*/
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Calibri,Helvetica,Arial, Helvetica, sans-serif;
}
p{
display: block;
margin-top: 1em;
margin-bottom: 1em;
margin-left: 0;
margin-right: 0;
}
h1	{display: block;
font-size: 2em;
margin-top: 0.67em;
margin-bottom: 0.67em;
margin-left: 0;
margin-right: 0;
font-weight: bold;	
}
h2	{display: block;
font-size: 1.5em;
margin-top: 0.83em;
margin-bottom: 0.83em;
margin-left: 0;
margin-right: 0;
font-weight: bold;
}	
h3	{display: block;
font-size: 1.17em;
margin-top: 1em;
margin-bottom: 1em;
margin-left: 0;
margin-right: 0;
font-weight: bold;
}	
h4	{display: block;
margin-top: 1.33em;
margin-bottom: 1.33em;
margin-left: 0;
margin-right: 0;
font-weight: bold;
}	
h5	{display: block;
font-size: .83em;
margin-top: 1.67em;
margin-bottom: 1.67em;
margin-left: 0;
margin-right: 0;
font-weight: bold;
}	
h6	{display: block;
font-size: .67em;
margin-top: 2.33em;
margin-bottom: 2.33em;
margin-left: 0;
margin-right: 0;
font-weight: bold;
}
/* ================= */
.visible{
    visibility: visible;
    display: block;
}
.hide{
    visibility: hidden;
    display: none;
}



.container{
    padding: 0;
    margin: 0 auto;
    background-color:black;
    min-height: 100vh;
    line-height: 1.9rem;
    word-spacing: .2rem;
    color:#999;
}
header{
    display: grid;
    margin: 0 auto;
    grid-template-columns: repeat(4,1fr);
    align-items: center;
    border-bottom: 1px solid #111;
    background-image: url(../images/headerback.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.head-left{
    grid-column: 1;
}
.logo{
    width:10rem;
    padding: 5px;
}
.head-right{
    color:#fff;
    grid-column: 2/4;
    display: grid;
    height: 100%;
    align-items: center;
    text-transform: uppercase;
    text-shadow: 3px 3px 10px white;
}
.head-right nav ul{
    display: flex;
    list-style: none;
}
.head-right nav ul li a{
    margin: 5px 20px 5px 0;
    text-decoration: none;
    font-size: 13px;
    text-transform: uppercase;
    color:white;
}
.head-right nav ul li a:hover{
    color: #d4d3d3;
    transition: color 300ms ease-in;
}
.head-right nav ul li a.active{
    color: red;
}
.place-holder{
    margin: 10px 0 0 0;
    background-color: #222;
    min-height: 60vh;
}
.wrapper{
    display: flex;
    padding: 0 15px 0 0;
    text-align: justify;
}
.wrapper span{
    font-size: 1.5rem;
    text-shadow: 2px 2px 3px black;
}
.profile-img img{
    width: 15rem;
    margin: 0 1rem 0 0;
}
/* SKILLS PAGE 
===========================================================*/
.skills{
    display: inline-block;
}

.skills li{
    padding-left: 35px;
    background-image: url(../images/bullet.png);
    background-position-x: left;
    list-style: none;
    background-repeat: no-repeat;
}


/* CONTACT PAGE 
===========================================================*/
.contact{
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: space-between;
    background-image: url(../images/contactback3.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color:#fff;
}
.contact-info{
    padding: 0 10px;
}
.contact-info p{
    margin: 0;
    margin-left: 40px;
    color:#999;
    line-height: normal;
}
.contact-info a{
    color:#999;
}
.contact-info a:hover{
    color:#fff;
}
.contact-info h4{
    margin-bottom: 2px;
}
.contact-info div{
    padding-bottom: 5px;
}
.contact-info i,.social-site-link i{
    font-size: 1rem;
    border-radius: 50%;
    padding: 10px;
    background:white;
    color: red;
}
.contact-info i.fa-map-marker-alt{
    padding: 10px 12px;
}
.contactForm{
    color:white;
    margin: 0 10px;
}
.contactForm form{
    position: relative;
}
.contactForm .inputBox input,
.contactForm .inputBox textarea{
    width: 100%;
    padding: 5px 0;
    margin:10px 0;
    border: none;
    border-bottom: 1px solid #dadada;
    outline: none;
    resize: none;
    background-color: transparent;
    color:#fff;
    font-size: 15px;
}
.contactForm .inputBox span{
    position: absolute;
    left: 0;
    padding: 5px 0;
    margin:10px 0;
    pointer-events: none;
    transition: 0.5s;
    color:#999;
    line-height: normal;
}
input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  -webkit-text-fill-color: #fff;
  -webkit-box-shadow: 0 0 0px 1000px transparent inset;
  transition: background-color 5000s ease-in-out 0s;
}

.contactForm .inputBox input:focus~span,
.contactForm .inputBox input:valid~span,
.contactForm .inputBox textarea:focus~span,
.contactForm .inputBox textarea:valid~span{
    color:red;
    font-size:12px;
    transform: translateY(-20px);
}
.contactForm .inputBox input[type="submit"]{
    cursor: pointer;
    width:100px;
    border:none;
    background-color: darkgray;
    color: white;
}
.contactForm .inputBox input[type="submit"]:hover{
    background-color: gray;
}
.contactForm .inputBox input[type="submit"].disable,
.contactForm .inputBox input[type="submit"].disable:hover{
    background-color: #111;
    color:#000;
    cursor: progress;
}
.submit-info{
    height: 50px;
    text-align: left;
}
.greensignal{
    background-image: url(../images/greensignal.png);
    color: green;
    text-shadow: 2px 2px 10px green;
    font-weight: bold;
    background-repeat: no-repeat;
    background-position: left;
    padding-left: 25px;
}
.redsignal{
    background-image: url(../images/redsignal.png);
    color: red;
    text-shadow: 2px 2px 10px red;
    font-weight: bold;
    background-repeat: no-repeat;
    background-position: left;
    padding-left: 25px;
}
.rotate{
    display: flex;
    align-items: center;
}
.spin{
    -webkit-animation:spin 4s linear infinite;
    -moz-animation:spin 4s linear infinite;
    animation:spin 4s linear infinite;
    width: 30px;
    height: 30px;
    margin-left: 20px;
}
@-moz-keyframes spin { 100% { -moz-transform: rotate(360deg); } }
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }




/*Footer
=========================================================================*/
footer{
    margin: 10px 0;
    border-top: 1px solid #111;

}
.social-site-link{
    text-align: center;
    padding: 10px 0;
}
.social-site-link h4{
    padding: 2px 0;
    margin: 0;
}
.social-site-link a{
    position: relative;
    text-decoration: none;
    margin: 0 10px;
}
.social-site-link a span{
    position: absolute;
/*     display: none;
    visibility: hidden; */
    top: -35px;
    left: -10px;
    font-size: 10px;
    background: black;
    line-height: normal;
    border: 1px dotted;
    padding: 5px 10px;
    opacity: 0;
    transition: opacity 500ms ease-out;
    color:#fff;   
}
.social-site-link a:hover span{
/*     display: inline;
    visibility: visible; */
    opacity: 1;
    transition: opacity 320ms ease-in;
}
.social-site-link a:hover i{
    background-color: #999;
}
.social-site-link i{
    font-size: 20px;
    padding: 8px;
}
.social-site-link i.fa-facebook{
    color:#4267B2;
}
.social-site-link i.fa-twitter{
    color:#1DA1F2;
}
.social-site-link i.fa-linkedin{
    color:#0077b5;
}

/* MEDIA QUERIES
===========================================================================*/

@media only screen and (min-width: 949px) {
    .container{
        width:950px;
    }
  }
  @media only screen and (max-device-width: 512px){
    header{
        display: flex;
        flex-direction: column;
    }
    .head-left a{
        line-height: 0;
        float: left;
    }
    .profile-img img {
        margin: 0;
        width: 60vw;
    }
    .logo{
        width: 50vw;
        padding: 0;
    }
    .head-right{
        display: flex;
        flex-direction: column;
        text-align: center;  
    }
    .head-right h3{
        padding: 0;
        margin: 0;
        line-height: 1.2rem;
        font-size: 15px;
    }
    .wrapper{
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        padding: 0 20px;
    }
    .contact{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .contactForm{
        width: 90%;
        text-align: center;
        border-top: 1px dotted;
        margin-top: 20px;
    }
  }